/* Broadcaster.css */

/* Contenedor del broadcaster */
.broadcaster-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  width: 100%;
  max-width: 500px;
  margin: 1rem 0;
}

/* Botón de iniciar transmisión */
.broadcast-btn {
  width: 100%;
  padding: 1.5rem;
  font-size: 1.25rem;
  font-weight: 700;
  border: none;
  border-radius: 1.5rem;
  cursor: pointer;
  color: #fff;
  background: linear-gradient(45deg, #ff416c, #ff4b2b);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.25);
  transition: all 0.3s ease;
}

/* Hover */
.broadcast-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 25px rgba(0, 0, 0, 0.35);
  background: linear-gradient(45deg, #ff4b2b, #ff416c);
}

/* Estado deshabilitado */
.broadcast-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Mensaje de transmisión activa */
.broadcasting-text {
  font-size: 1.2rem;
  font-weight: 600;
  color: #fff;
  margin-top: 1rem;
}

/* Responsive para móviles */
@media (max-width: 480px) {
  .broadcast-btn {
    padding: 1rem;
    font-size: 1rem;
  }
}
